}

@media (width >= 1024px) {
  .nav__list {
    gap: 48px;
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  }

  .nav__list .nav__link {
    color: #fff;
    letter-spacing: 3px;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 700;
    line-height: 19.5px;
    text-decoration: none;
  }

  .nav__list .nav__link:after {
    content: "";
    background-color: #2060f6;
    border-radius: 4px;
    width: 100%;
    height: 2px;
    transition: all .3s;
    display: block;
    position: relative;
    bottom: -8px;
    transform: scale(0);
  }

  .nav__list .nav__link:hover:after {
    transform: scale(1);
  }
}

